@import url("https://fonts.googleapis.com/css?family=Rubik:300&subset=cyrillic");
* {
  font-family: 'Rubik', sans-serif;
  font-weight: 300;
  font-size: 20px;
  color: #1c1919; }

body {
  padding-top: 50px;
  background: #eeeff7; }

a {
  text-decoration: none;
  color: #1c1919; }

a:hover,
a:focus {
  color: #1c1919;
  text-decoration: none; }

li {
  list-style-type: none; }

ul {
  margin-left: 0;
  padding-left: 0; }

button {
  background: none;
  border: none; }

@media (max-width: 767px) {
  body {
    padding: 0; } }

.navbar {
  background: #1c1919;
  position: fixed;
  width: 100%;
  height: 50px;
  line-height: 50px;
  top: 0; }
  .navbar-brand {
    color: #eeeff7;
    font-weight: 600; }
  .navbar-brand:hover, .navbar-brand:focus {
    color: #eeeff7; }

.menu-toggle {
  float: right; }

.sidebar {
  position: fixed;
  top: 50px;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: block;
  padding: 0;
  overflow-x: hidden;
  overflow-y: auto;
  background: #403d3c; }
  .sidebar a {
    color: #c6c7ce;
    padding-left: 20px;
    text-transform: uppercase;
    transition: 0.3s; }
  .sidebar .svg {
    vertical-align: top;
    stroke: #c6c7ce;
    margin-right: 10px;
    transition: 0.3s; }
  .sidebar li:hover a {
    color: white;
    padding-left: 34px; }
  .sidebar li:hover .svg {
    stroke: white; }
  .sidebar li {
    padding-top: 15px;
    padding-bottom: 15px;
    margin-bottom: 5px;
    background: #534f4e;
    transition: 0.3s; }
  .sidebar .active {
    border-left: 6px solid #eb5937; }
    .sidebar .active a {
      color: white;
      padding-left: 34px; }
    .sidebar .active .svg {
      stroke: white; }

@media (max-width: 767px) {
  .navbar {
    position: relative;
    top: 0;
    left: 0;
    right: 0; }
  .sidebar {
    position: relative;
    bottom: none;
    left: -15px;
    right: 0;
    width: 100vw;
    top: -2px; } }
